.coming-soon-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to bottom, #ff9d7f, #f0f0f0);
    text-align: center;
    font-family: Arial, sans-serif;
  }

  img {
    width: 20rem;
  }
  
  .coming-soon-title {
    font-size: 3rem;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .coming-soon-text {
    font-size: 1.5rem;
    color: #555;
    margin-bottom: 2rem;
  }
  
  .coming-soon-footer {
    font-size: 1rem;
    color: #888;
  }
  
  .coming-soon-footer a {
    color: #007bff;
    text-decoration: none;
  }
  
  .coming-soon-footer a:hover {
    text-decoration: underline;
  }
  